.gallery {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 20vw 20vw 20vw;
      grid-template-columns: 20vw 20vw 20vw;
  gap: 2vw;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border: 5px solid green;
}

.gallery .popup {
  width: 70vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  border: 2px solid yellow;
}

.gallery .popup img,
.gallery .popup iframe {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.gallery .imageDiv {
  width: 100%;
  height: 100%;
  border: 3px solid blue;
}

.gallery .imageDiv img,
.gallery .imageDiv iframe {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
